










import Vue from 'vue'
import {supportPositions, supportModes} from "../filters/CropFilter"

export default Vue.extend({
  props: ["settings"],
  data() {
    return {
      supportPositions: Object.entries(supportPositions).filter(([position]) => isNaN(Number(position))).map(([position, index]) => {return {"text": position, "value": index}}),
      supportModes: Object.entries(supportModes).filter(([mode]) => isNaN(Number(mode))).map(([mode, index]) => {return {"text": mode, "value": index}}),
    }
  },
})
